-->
<template>
  <div class="through-car-table">
    <a-table
      class="default-table"
      :pagination="false"
      :columns="columns"
      :data-source="vehicleData"
      rowKey="no"
      :customRow="click"
      :scroll="{ y: 'calc(90vh - 16.9vw))' }"
    >
      <!-- 表格中车牌号插槽 start -->
      <span slot="card" slot-scope="text, record">
        <div class="blue" v-if="record.type == 0">
          {{ text }}
        </div>
        <div class="green" v-else-if="record.type == 1">
          {{ text }}
        </div>
        <div class="yellow" v-else-if="record.type == 2">
          {{ text }}
        </div>
      </span>
      <!-- 表格中车牌号插槽 end -->

      <!-- 表格中车辆抓拍图插槽 start -->
      <span slot="img">
        <img class="card-img" src="../../assets/img/charts/img-car.png" />
      </span>
      <!-- 表格中车辆抓拍图插槽 end -->
    </a-table>
  </div>
</template>

<script>
/**
 * 表头
 */
const columns = [
  {
    title: "序列",
    dataIndex: "no",
    key: "no"
  },
  {
    title: "车牌号",
    key: "card",
    dataIndex: "card",
    scopedSlots: { customRender: "card" }
  },
  {
    title: "车辆抓拍图",
    dataIndex: "img",
    key: "img",
    scopedSlots: { customRender: "img" }
  },
  {
    title: "号牌种类",
    dataIndex: "carType",
    key: "carType"
  },
  {
    title: "车牌颜色",
    dataIndex: "cardColor",
    key: "cardColor"
  },
  {
    title: "车身颜色",
    dataIndex: "carColor",
    key: "carColor"
  },
  {
    title: "通行时间",
    dataIndex: "time",
    key: "time",
    width: "14vw",
    ellipsis: true
  },
  {
    title: "位置",
    dataIndex: "address",
    key: "address",
    width: "13vw",
    ellipsis: true
  }
];

export default {
  props: {
    /**
     * 车辆数组
     */
    vehicleData: {
      type: Array,
      default: () => {
        return [];
      }
    }
  },
  data() {
    return {
      columns
    };
  },
  methods: {
    /**
     * 表格点击事件
     */
    click(record, index) {
      return {
        on: {
          click: () => {
            this.$emit("openModal");
          }
        }
      };
    }
  }
};
</script>
