<template>
  <div class="through-car-details">
    <modal
      title="车辆通行信息"
      :visible="visible"
      width="46.094vw"
      height="26.25vw"
      @close="$emit('close')"
    >
      <div slot="modalBg" class="modal-bg"></div>
      <div class="details">
        <div class="left">
          <triangle-box>
            <img src="../../assets/img/charts/img-modal-car.png" />
          </triangle-box>
        </div>
        <div class="right">
          <div class="item">
            <div class="lable">车牌号:</div>
            <a-input class="display-frame" disabled value="皖A 6MM88"></a-input>
          </div>
          <div class="item">
            <div class="lable">车牌颜色:</div>
            <a-select
              class="display-frame display-frame-select"
              disabled
              default-value="1"
              dropdown-class-name="default-select-dropdown"
              :get-popup-container="triggerNode => triggerNode.parentNode"
            >
              <a-select-option value="1">
                蓝色
              </a-select-option>
            </a-select>
          </div>
          <div class="item">
            <div class="lable">号牌种类:</div>
            <a-select
              class="display-frame display-frame-select"
              disabled
              default-value="1"
              dropdown-class-name="default-select-dropdown"
              :get-popup-container="triggerNode => triggerNode.parentNode"
            >
              <a-select-option value="1">
                小型汽车
              </a-select-option>
            </a-select>
          </div>
          <div class="item">
            <div class="lable">车身颜色:</div>
            <a-select
              class="display-frame display-frame-select"
              disabled
              default-value="1"
              dropdown-class-name="default-select-dropdown"
              :get-popup-container="triggerNode => triggerNode.parentNode"
            >
              <a-select-option value="1">
                灰色
              </a-select-option>
            </a-select>
          </div>
          <div class="item">
            <div class="lable">位置:</div>
            <a-select
              class="display-frame display-frame-select"
              disabled
              default-value="1"
              dropdown-class-name="default-select-dropdown"
