              </a-select-option>
            </a-select>
          </div>
          <div class="item">
            <div class="lable">通行时间:</div>
            <a-date-picker
              format="YYYY-MM-DD hh:mm:ss"
              :value="moment('2021-04-20 20:21:04', 'YYYY-MM-DD hh:mm:ss')"
              disabled
              class="default-date-picker display-frame"
            />
          </div>
        </div>
      </div>
    </modal>
  </div>
</template>

<script>
import TriangleBox from "../../components/TriangleBox";
import Modal from "../../components/Modal";

import moment from "moment";

export default {
  components: {
    TriangleBox,
    Modal
  },
  props: {
    // 对话框是否可见
    visible: {
      type: Boolean,
      default: false
    }
  },
  methods: {
    moment
  }
};
</script>

